    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main_container {
    width: 500px;
    padding: 50px;
    border: 1px solid black;
}

.main_container input {
    display: block;
    width: 100%;
    height: 30px;
    border: 1px dotted red;
    margin-top: 10px;
}

/* todo 리스트 아이템 */
.todo_list_wrap {
    margin-top: 20px;
}

.todo_item {
    cursor: pointer;
    padding: 10px;
    border-bottom: 1px dotted black;
}

.todo_item.completed span {
    text-decoration: line-through;
    opacity: 0.5;
}

.todo_item_wrap {
    display: flex;
    justify-content: space-between;
}


/* 하단 버튼 영역 */
.status_wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.status_wrap[data-mode="all"] .show_all_btn{
    color: red;
}

.status_wrap[data-mode="active"] .show_active_btn{
    color: red;
}

.status_wrap[data-mode="completed"] .show_completed_btn{
    color: red;
}`, "",{"version":3,"sources":["webpack://./src/css/main.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,iBAAiB;IACjB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,cAAc;IACd,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA,iBAAiB;AACjB;IACI,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,aAAa;IACb,+BAA+B;AACnC;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;;AAGA,aAAa;AACb;IACI,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,UAAU;AACd","sourcesContent":[".main {\n    width: 100%;\n    min-height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.main_container {\n    width: 500px;\n    padding: 50px;\n    border: 1px solid black;\n}\n\n.main_container input {\n    display: block;\n    width: 100%;\n    height: 30px;\n    border: 1px dotted red;\n    margin-top: 10px;\n}\n\n/* todo 리스트 아이템 */\n.todo_list_wrap {\n    margin-top: 20px;\n}\n\n.todo_item {\n    cursor: pointer;\n    padding: 10px;\n    border-bottom: 1px dotted black;\n}\n\n.todo_item.completed span {\n    text-decoration: line-through;\n    opacity: 0.5;\n}\n\n.todo_item_wrap {\n    display: flex;\n    justify-content: space-between;\n}\n\n\n/* 하단 버튼 영역 */\n.status_wrap {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 20px;\n}\n\n.status_wrap[data-mode=\"all\"] .show_all_btn{\n    color: red;\n}\n\n.status_wrap[data-mode=\"active\"] .show_active_btn{\n    color: red;\n}\n\n.status_wrap[data-mode=\"completed\"] .show_completed_btn{\n    color: red;\n}"],"sourceRoot":""}]);
